.routen_panel{
    width: 450px;
    height: 260px;
    display: flex;
    flex-direction: column;
    background-color: var(--color-gray-1);
    margin-bottom: 1.5em;
    margin-left: 1em;
    margin-right: 1em;
    padding: 1em;
    border-radius: 10px;
}

.routen_panel_div1{
    display: flex;
    flex-direction: row;
    padding-bottom: 1em;
    justify-content: space-between;
}

.routen_panel_div1 p{
    font-size: large;
}

.routen_panel_route_date_and_color{
    display: flex;
    flex-direction: column;

}


.routen_panel_route_date_and_color > p{
    padding-bottom: 10px;
    
}

.routen_panel_hide{
    width: 50px;
    height: 48px;
    display: flex;
    justify-content: center;
    align-items: center;
    align-content: center;
    padding-right: 10px;
}



.routen_panel_div2_grouped{
    
    display: flex;
    flex-direction: column;
}


.routen_panel_div2{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}


.routen_panel_div2_1{
    display: flex;
    flex-direction: row;
    padding-bottom: 1em;
    align-items: center;
}


.eco_rating{
    font-size: 34px;
    font-weight: bold;
    padding-left: 10px;
}

.eco_difference{
    padding-left: 16px;
}

.routen_panel_div2_2{
    display: flex;
    flex-direction: row;
    padding-bottom: 1em;
    align-items:center;
}

.routen_panel_eco_rating{
    display: flex;
    flex-direction: column;
}

.routen_panel_eco_rating > p {
    padding-left: 10px;
}

.routen_panel_eco_rating_value{
    display: flex;
    flex-direction: row;
    align-items: center;
}

.routen_panel_time{
    display: flex;
    flex-direction: column;
}

.routen_panel_time > p{
    padding-left: 16px;
}

.time{
    font-size: 34px;
    font-weight: bold;
    padding-left: 10px;
    
}

.time_difference{
    padding-left: 16px;
}

.routen_panel_eco_rating_value{
    display: flex;
    flex-direction: row;
    align-items: center;
}

.routen_divider{
    max-width: 60px;
}

.routen_panel_time_image > img{
    width: 48;
    height: 48;
}

#time_pic{
    width: 48;
    height: 48;
}

.eco_rating_comparison{
    padding-left: 1.5em;
    display: flex;
    flex-direction: row;
}

.eco_rating_comparison > img{
    padding-right: 2px;
}

.time_comparison{
    padding-left: 8px;
    display: flex;
    flex-direction: row;
}

.time_comparison > img{
    padding-right: 2px;
}

.routen_panel_arrow{
    padding-bottom: 4em;
}